import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
    count: 0,
    selectList:[],     //
    isDrawer:false,     //drawer
    addFrom:{
          id:'',
          number: '',
          name: '',
          city: '',
          birth: '',
          sex: '',
          age: 1,
          place: ''
    },  //
    isAddTypes:false,   //  falseture
    countRUL:'http://localhost:8081/frame/count/',  // url
    sizeForm:{
        name: '',
        find: 'dim',
        dates: [],
        agestart: 0,
        ageend: 0,
        sex: '',
        checkedCities: [],
        pageNo:1,
        pageSize:10
    },  //
    queryChooseList:[], //
    currentPage:0  //  list
}
const mutations={
    addFromchangNull(state){    // 
        state.addFrom.id = ''
        state.addFrom.number = ''
        state.addFrom.name =''
        state.addFrom.city =''
        state.addFrom.birth= ''
        state.addFrom.sex= ''
        state.addFrom.age= 1,
        state.addFrom.place= ''
    }
}

export default new Vuex.Store({
    state,
    mutations
})


