<template>
  <div class="wrapper">
    <el-form ref="form" :model="form" label-width="80px">
  <el-form-item label="">
    <el-input v-model="form.number"></el-input>
  </el-form-item>
  <el-form-item label="">
    <el-input v-model="form.name"></el-input>
  </el-form-item>
  <el-form-item label="">
    <el-select v-model="form.city" placeholder="">
      <el-option label="" value=""></el-option>
      <el-option label="" value=""></el-option>
      <el-option label="" value=""></el-option>
      <el-option label="" value=""></el-option>
    </el-select>
  </el-form-item>
  <el-form-item label="">
    <el-col :span="11">
      <el-date-picker type="date" placeholder="" v-model="form.birth" style="width: 100%;" value-format="yyyy-MM-dd"></el-date-picker>
    </el-col>
  </el-form-item>
  <el-form-item label="">
      <el-input-number v-model="form.age" :min="1" :max="150"></el-input-number>
  </el-form-item>
  <el-form-item label="">
    <el-radio-group v-model="form.sex">
      <el-radio label=""></el-radio>
      <el-radio label=""></el-radio>
    </el-radio-group>
  </el-form-item>
  <el-form-item label="">
    <el-input type="textarea" v-model="form.place"></el-input>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" v-if="!isAddTypes" @click="onToAdd"></el-button>
    <el-button type="primary" v-if="isAddTypes" @click="onToUpdate"></el-button>
    <el-button @click="onCancel"></el-button>
  </el-form-item>
</el-form>
           
  </div>
</template>

<script>
export default {
  components:{},
  props:{},
  data(){
    return {
        form:this.$store.state.addFrom ,
        isAddTypes:this.$store.state.isAddTypes
    }
  },
  watch:{
    '$store.state.addFrom':function(){
        this.form = this.$store.state.addFrom
      //  console.log("addFrom-Addfrom")
    },
    '$store.state.isAddTypes':function(){
        this.isAddTypes = this.$store.state.isAddTypes
      //  console.log("addFrom-isAddTypes")
    }
  },
  computed:{},
  methods:{
     onToAdd() { // 
        var url = this.$store.state.countRUL+'addCountCon'
        var self = this
        this.$ajax.post(url,this.form).then(function (response) {  //
          console.log(""+response);
          self.$store.commit('addFromchangNull')  //addFrom
        }).catch(function (error) { //
          console.log(""+error);
        });
      },

      onToUpdate(){ // 
        var url =this.$store.state.countRUL+'updateCountCon'
        this.$ajax.post(url,this.form).then(function (response) { //
          alert(response.data)
        }).catch(function (error) { //
          alert(""+error);
        });
      },

      onCancel(){ //
        // console.log("onCancel")
        this.$store.state.isDrawer = false
        this.$forceUpdate(); 
      }
  },
  created(){},
  mounted(){
  }
}
</script>
<style scoped>

</style>


